@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
    margin: 0;
    background: rgb(19, 19, 19);
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
}

* {
  font-family:'Montserrat', sans-serif;
}


.about img {
  transition: all 500ms;
} 

.about img:hover {
  -webkit-transform: translateY(-3px) scale(1.05);
          transform: translateY(-3px) scale(1.05);
}

i {
  transition: all 200ms;
}

i:hover{
  -webkit-transform: translateY(-3px) scale(1.2);
          transform: translateY(-3px) scale(1.2);
}


.nav-bar > a:hover {
    color : white !important;
}

.contact-info > div {
    transition: all 300ms;
}

.contact-info > div:hover {
    color : goldenrod;
    -webkit-transform: translateY(-3px) scale(1.2);
            transform: translateY(-3px) scale(1.2);
}

.contact-info i {
    color : goldenrod;
}

.contact-info {
    margin-top: 120px !important;
}
.jumbo {
    max-width: 70%;
    margin: 5rem auto 0 auto;
    background-color: black;
    display : flex;
    flex-direction: column;
    align-items: center;
}

.jumbo div {
    margin-top: 1rem;
}

.jumbo h2 {
    text-align: center;
    color : grey;
}

.jumbo hr {
    background-color: honeydew;
    opacity: 0.2;
}

.tech-stack img {
    width : 120px;
    padding : 15px;
}

.tech-stack .bit-smaller{
    width : 115px;
}

.tech-stack .bit-smaller:hover{
    width : 120px;
}

.proficiency { 
    display : flex;
    flex-direction: row;
    justify-content: space-between;
}

.commit-hist {
    margin-top: 5rem;
}

.tech-stack img:hover {
    width : 123px;
}

.tools img:hover {
    width : 123px;
}

.tools .bit-smaller{
    width : 115px;
}

.tools .bit-smaller:hover {
    width : 120px;
}
.about {
    color : whitesmoke;
    margin-top : 4rem;
    display : flex;
    flex-direction : row;
    justify-content : center;
    align-items : center,
}

.fix-height {
    flex-shrink: 3;
    min-height: 330px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.fix-height-left{
    margin-left : 3rem;
    min-height: 330px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
}

.on-hover {
    transition: all 200ms;
}

.devIcon:hover{
    -webkit-transform: translateX(-3px) scale(1.1) !important;
            transform: translateX(-3px) scale(1.1) !important;
}


